import CONFIG from '../../global/config';

const RegistrasiDzaky = {
  async render() {
    return `
        <main class="mx-auto bg-gradient-primary col-lg-4 container row justify-content-center col-md-60 col-lg-6 card o-hidden border-0 card-body p-5" style="background-color: transparent;">
          <section class="text-center">
            <h1 class="h4 text-gray-900 mb-4 fw-bolder">Registrasi</h1>
          </section>
          <form id="registerForm" class="user">
            <section class="form-group">
              <label for="username" style="color:#4D869C">Username</label>
              <input type="text" id="username" minlength="6" name="username" required pattern=".{6,}" class="form-control form-control-user" style="border-radius: 20px;" placeholder="Arjuna" required>
            </section>
            <br>
            <section class="form-group">
              <label for="lokasi" style="color:#4D869C">Lokasi</label>
              <input type="text" id="lokasi" minlength="4" name="lokasi" class="form-control form-control-user" style="border-radius: 20px;" placeholder="Palu" required>
            </section>
            <br>
            <section class="form-group">
              <label for="noWa" style="color:#4D869C">No Whatsapp</label>
              <input type="text" id="noWa" minlength="9" name="noWa" class="form-control form-control-user" style="border-radius: 20px;" placeholder="0812334567" required>
            </section>
            <br>
            <section class="form-group">
              <label for="password" style="color:#4D869C">Password</label>
              <input type="password" minlength="6" id="password" name="password" class="form-control form-control-user" style="border-radius: 20px;" placeholder="secr3t" required>
            </section>
            <br>
            <section class="form-group">
              <label for="ulangipassword" style="color:#4D869C">Ulangi Password</label>
              <input type="password" minlength="6" id="ulangipassword" name="ulangipassword" class="form-control form-control-user" style="border-radius: 20px;" placeholder="secr3t" required>
            </section>
            <br>
            <section class="form-group">
              <label for="peran" style="color:#4D869C">Registrasi sebagai</label>
              <select id="peran" name="peran" class="form-control rounded-5" required>
                <option value="user">User</option>
                <option value="admin">Admin</option>
              </select>
            </section>
            <br>
            <button type="submit" id="btnRegistrasi" class="btn btn-primary btn-user btn-block" style="border-radius: 20px; width: 100%; color: #FFFFFF; background-color: #4D869C;">Registrasi</button>
          </form>
          <br>
          <section class="text-center">
            <p style="color:#000;">Sudah memiliki akun? <a href="#/login" style="color:#00008B; text-decoration: underline;">Silahkan login !!</a></p>
          </section>
          <br>
        </main>
      `;
  },

  async afterRender() {
    const btnRegistrasi = document.getElementById('btnRegistrasi');

    btnRegistrasi.addEventListener('click', async (event) => {
      event.preventDefault();

      const username = document.getElementById('username').value;
      const lokasi = document.getElementById('lokasi').value;
      const noWa = document.getElementById('noWa').value;
      const password = document.getElementById('password').value;
      const ulangipassword = document.getElementById('ulangipassword').value;
      const peran = document.getElementById('peran').value;

      // Validasi panjang minimum
      if (
        username.length < 6 ||
        lokasi.length < 4 ||
        noWa.length < 9 ||
        password.length < 6 ||
        ulangipassword.length < 6
      ) {
        alert(
          'Pastikan semua kolom sudah diisi dengan panjang minimal yang sesuai.'
        );
        return;
      }

      // Validasi password dan ulangipassword sama
      if (password !== ulangipassword) {
        alert('Password dan Ulangi Password harus sama!');
        return;
      }

      try {
        // Check if username or noWa already exist
        const response = await fetch(
          `https://fuzzy-mag-bidwi-7f3836df.koyeb.app/pengguna?api_key=${CONFIG.KEY}`
        );
        const responseData = await response.json();

        if (!response.ok) {
          throw new Error(
            responseData.message ||
              'Gagal memeriksa ketersediaan username dan noWa.'
          );
        }

        const existingUser = responseData.data.infoPengguna.find(
          (user) => user.username === username || user.noWa === noWa
        );
        if (existingUser) {
          alert('Username atau No Whatsapp sudah terdaftar!');
          return;
        }

        const payload = {
          username,
          lokasi,
          noWa,
          password,
          peran,
        };

        const registerResponse = await fetch(
          `https://fuzzy-mag-bidwi-7f3836df.koyeb.app/pengguna?api_key=${CONFIG.KEY}`,
          {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify(payload),
          }
        );

        const registerData = await registerResponse.json();
        if (registerResponse.ok) {
          console.log('Registrasi berhasil:', registerData);
          alert('Registrasi berhasil!');
          localStorage.setItem('UserId', registerData.data.infoId);
          window.location.href = '#/login';
        } else {
          console.error('Registrasi gagal:', registerData);
          alert(`Registrasi gagal: ${registerData.message}`);
        }
      } catch (error) {
        console.error('Terjadi kesalahan:', error);
        alert('Terjadi kesalahan. Silakan coba lagi.');
      }
    });
  },
};

export default RegistrasiDzaky;
